<template>
<div class="contain" id="contain">
    <div class="link">
        <ul>
            <li v-for="item in list">
                <div class="line"></div>
                <a :href="item.url" target="_blank" class="flow-wave">
                    <div class="name">{{item.name}}</div>
                    <div class="site">{{item.url}}</div>
                </a>
            </li>
           
        </ul>
    </div>
</div>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';
import "../assets/js/duangduangduang"
export default {
data() {
//这里存放数据
return {
    list:[
        {name:"前端资源站",url:"http://www.fly63.com/nav"},
        {name:"blog",url:"http://blog.nowcoder.net/c9527"},
        {name:"CSS ICON",url:"https://cssicon.space/"},
        {name:"字体图标",url:"https://icomoon.io/"},
        {name:"油猴插件",url:"https://greasyfork.org/zh-CN"},
        {name:"无忧论坛",url:"http://bbs.wuyou.net/"},
        {name:"果汁音乐",url:"http://guozhivip.com/yinyue/"},
        {name:"樱云 Sakura Cloud",url:"https://tawk.to/chat/5d3441149b94cd38bbe87c81/default"},


    ]
};
},
methods: {

},
}

</script>
<style scoped>
@import "../assets/css/collection.css";
#contain{
            padding: 0;
            background-color: rgba(0,0,0,.7);
        }
        #contain:after{
            content: "";
            position: absolute;
            left: 0;
            right: 0;
            top: 0;
            bottom: 0;
            background: url("../assets/img/bgImg2.jpg") no-repeat;
            background-size: 100% 100%;
            opacity: .3;
            /*z-index: 0;*/
        }
        .head{
            background-color: rgba(1,1,1,0) !important;
        }
</style>


